body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.tile-title {

}

.about {
    grid-area: about;
    background-color: lightgreen;
}

.projects {
    grid-area: projects;
    background-color: lightblue;
}

.tech {
    grid-area: tech;
    background-color: lightcoral;
}

.contact {
    grid-area: contact;
    background-color: lightslategray;
}

.resume {
    grid-area: resume;
    background-color: lightyellow;
}

.page-wrapper {
    height: 100vh;
    width: 100vw;
    justify-content: center;
    max-width: 100%;
    text-align: center;
    max-height: 100%;
    background-color: whitesmoke;
}

.grid-container {
    display: grid;
    grid-template-areas: 'about about about projects projects projects' 'tech tech tech projects projects projects' 'resume resume contact contact contact contact';
    grid-gap: 40px;
    height: 91%;
    width: 93%;
    margin: 0 auto;
    padding: 3% 0;
}
.grid-container > div {
    text-align: center;
    font-size: 30px;
    position: relative;
    box-shadow: 2px 1px 1px lightgrey;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.grid-container > div:before {
     content: '';
     position: absolute;
     border: white solid 12px;
     top: 5px;
     left: 5px;
     right: 5px;
     bottom: 5px;
     opacity: .4;
    -webkit-transition-duration: 0.2s;
    transition-duration: 0.2s;
    -webkit-transition-property: border;
    transition-property: border;
 }

.grid-container > div:hover:before {
    border: white solid 20px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

